<template>
  <div class="flex-1 flex-box flex-column"
      v-loading="loading"
      element-loading-text="定位中"
      element-loading-spinner="el-icon-loading"
      element-loading-background="rgba(0, 0, 0, 0.8)"
    >
    <BaiduMap
      class="flex-1 bm-view"
      ak="6IgUio3pgB7dHoQkgllpi9voF4nkxuMH"
      :center="bmCenter"
      :zoom="15"
      :dragging="false"
      :map-click="false"
      :scroll-wheel-zoom="false"
      :double-click-zoom="false"
      :keyboard="false"
      :pinch-to-zoom="false"
    >
      <BmGeolocation anchor="BMAP_ANCHOR_BOTTOM_RIGHT" :showAddressBar="true" :autoLocation="true" @ready="handler" />
      <BmMarker :position="bmCenter" />
      <BmLocalSearch :keyword="keyword" :panel="false" @searchcomplete="searchcomplete" />
    </BaiduMap>
    <div class="result flex-box flex-column">
      <div class="flex-box result-search">
        <el-input placeholder="搜索地点" v-model="keyword" clearable></el-input>
      </div>
      <div v-if="bmList.length < 1" class="result-empty">无地址</div>
      <div class="flex-1 scroll result-list">
        <div class="flex-box items-center result-item" :key="index" v-for="(item, index) in bmList" @click="handleSetCenter(item.point, item.title)">
          <div class="flex-1">
            <div class="result__title">{{item.title}}</div>
            <div class="result__address">{{item.address}}</div>
          </div>
          <div v-if="bmCenter.lng === item.point.lng && bmCenter.lat === item.point.lat && bmTitle === item.title" class="el-icon-check result-icon"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { BaiduMap, BmGeolocation, BmMarker, BmLocalSearch } from 'vue-baidu-map'
export default {
  name: 'BaiduMaps',
  components: {
    BaiduMap,
    BmGeolocation,
    BmMarker,
    BmLocalSearch
  },
  data () {
    return {
      loading: false,
      bmCenter: {
        lng: 116.403963,
        lat: 39.915119
      },
      bmList: [],
      bmTitle: '',
      keyword: ''
    }
  },
  methods: {
    handler ({ BMap, map }) {
      this.loading = true
      const geolocation = new BMap.Geolocation()
      geolocation.getCurrentPosition(GeolocationResult => {
        const { point } = GeolocationResult
        // 创建地理编码（地理解析器）实例
        const myGeo = new BMap.Geocoder()
        // 根据坐标得到地址描述
        myGeo.getLocation(point, result => {
          if (result) {
            const { point, surroundingPois } = result
            if (surroundingPois.length > 0) {
              this.bmList = surroundingPois
              this.bmCenter = surroundingPois[0].point
              this.bmTitle = surroundingPois[0].title
            } else {
              this.bmList = result
              this.bmCenter = point
              this.bmTitle = ''
            }
          } else {
            this.$message.error('没有获取到地址')
          }
          this.loading = false
        }, {
          poiRadius: 1000,
          numPois: 8
        })
      }, {
        enableHighAccuracy: true,
        maximumAge: 1000
      })
    },
    searchcomplete (results) {
      if (!results) return
      const { Hr = [] } = results
      this.bmList = Hr
    },
    handleSetCenter (point, title) {
      this.bmTitle = title
      this.bmCenter = point
    }
  }
}
</script>

<style scoped lang="less">
.bm-view {
  ::v-deep .anchorBL {
    display: none;
  }
}
.result {
  max-height: 60%;
  background: rgba(255,255,255,.9);
}
.result-search {
  padding: 10px 5px;
  ::v-deep input {
    font-size: 16px;
  }
}
.result-search__button {
  padding-left: 5px;
}
.result-list {
  overflow: auto;
  padding-left: 10px;
}
.result-item {
  padding: 8px 10px 8px 0;
  border-bottom: 1px solid #ededed;
}
.result__title {
  font-size: 16px;
  color: #000;
}
.result__address {
  color: #777;
}
.result-icon {
  padding-left: 10px;
  text-align: center;
  color: #2ba245;
  font-weight: bold;
  font-size: 16px;
}
.result-empty {
  text-align: center;
  padding-bottom: 10px;
}
</style>
